.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

#root {
  font-family: arial;
  margin: 0 auto;
  max-width: 340px;
  min-height: 600px;
    /* background-color: #81C3DB; */
    background: -webkit-linear-gradient(340deg, #e2ebf0
 60%, #cfd9df 60%);
    background: -o-linear-gradient(340deg, #e2ebf0
 60%, #cfd9df 60%);
    background: linear-gradient(110deg, #e2ebf0
 60%, #cfd9df 60%);
    padding-bottom: 5px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



